.user {
    display: flex;
    width: 640px;
    height: 55px;
    background: #F8F8F7;
    border-radius: 10px;
    margin-top: 10px;
    padding: 10px;
    div {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 35px;
        width: 60px;
        background-color: #fff;
        margin-right: 30px;
        font-size: 18px;
    }
    p {
        margin: 0;
        height: 35px;
        line-height: 35px;
        font-weight: 500;
        font-size: 18px;
        color: #8C8C8C;
        span {
            color: #1D1D1D;
        }
    }
    img {
        display: block;
        width: 20px;
        height: 20px;
        margin: auto 0 auto auto;
    }
}
